<template>
  <div class="administracion mt-5">
    <b-container>
      <h1>Administración</h1>
      <b-button variant="primary" class="mt-5" v-b-modal.modal-1>AGREGAR CURSO</b-button>
      <table class="table table-hover mt-5">
        <thead>
          <tr>
            <th>Curso</th>
            <th>Cupos</th>
            <th>Inscritos</th>
            <th>Duración</th>
            <th>Costo</th>
            <th class="th-terminado">Terminado</th>
            <th>Fecha</th>
            <th>Acciones</th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="curso in datos" :key="curso.id">
            <td>{{ curso.nombre }}</td>
            <td>{{ curso.cupos }}</td>
            <td>{{ curso.inscritos }}</td>
            <td>{{ curso.duracion }}</td>
            <td>
              <p class="td-verde">$ {{ curso.costo }}</p>
            </td>
            <td>
              <p :class="{ 'td-azul': curso.completado, 'td-gris': !curso.completado }">{{ curso.completado ? 'Sí' : 'No'
              }}</p>
            </td>
            <td>
              <p class="td-verde-fecha">{{ curso.fecha_registro }}</p>
            </td>
            <td><b-button class="btnAcction" v-on:click.prevent="openModal2(curso.id)"><b-icon icon="pencil-fill"
                  variant="warning"></b-icon></b-button>
              <b-button class="btnAcction" v-on:click.prevent="eliminarC(curso.id)"><b-icon icon="trash-fill"
                  variant="danger"></b-icon></b-button>
            </td>
          </tr>
        </tbody>
      </table>
      <div v-if="resumen">
        <ResumenCursos></ResumenCursos>
      </div>
      <br>
      <div>
        <b-modal id="modal-1" title="Agregar Curso" hide-footer ref="my-modal" hide-header>
          <b-form @submit="onSubmit" @reset="onReset" v-if="show">
            <b-form-input id="nombre" type="text" v-model="form.nombre" required placeholder="Nombre"></b-form-input><br>
            <b-form-input id="img" v-model="form.img" type="text" required
              placeholder="URL de la imagen"></b-form-input><br>
            <b-form-group id="input-group-1" label="Cupos del curso" label-for="cupo">
              <b-form-input id="cupo" v-model="form.cupos" type="number" required placeholder=""></b-form-input><br>
            </b-form-group>
            <b-form-group id="input-group-2" label="Inscritos del curso" label-for="inscritos">
              <b-form-input id="inscritos" v-model="form.inscritos" type="number" required
                placeholder=""></b-form-input><br>
            </b-form-group>
            <b-form-input id="duracion" v-model="form.duracion" type="text" required
              placeholder="Duración del curso"></b-form-input><br>
            <b-form-group id="input-group-3" label="Fecha de registro" label-for="fecha">
              <b-form-input id="fecha" v-model="form.fecha_registro" type="text" placeholder=""
                readonly></b-form-input><br>
            </b-form-group>
            <b-form-group id="input-group-4" label="Costo del curso" label-for="costo">
              <b-form-input id="costo" v-model="form.costo" type="text" required placeholder=""></b-form-input><br>
            </b-form-group>
            <b-form-textarea id="descripcion" v-model="form.descripcion" placeholder="Descripcón del curso" rows="3"
              max-rows="6" required></b-form-textarea> <br>
            <b-button type="submit" variant="success">AGREGAR</b-button>
            <b-button type="reset" variant="warning">LIMPIAR FORMULARIO</b-button>
            <b-button variant="danger" v-on:click.prevent="hideModal">CANCELAR</b-button>
          </b-form>
        </b-modal>
      </div>
      <div>
        <b-modal id="modal-2" title="Editar Curso" hide-footer ref="my-modal2" hide-header>
          <b-form @submit="edit" v-if="show2">
            <b-form-input id="nombre" type="text" v-model="form2.nombre" required placeholder="Nombre"></b-form-input><br>
            <b-form-input id="img" v-model="form2.img" type="text" required
              placeholder="URL de la imagen"></b-form-input><br>
            <b-form-group id="input-group-1" label="Cupos del curso" label-for="cupo">
              <b-form-input id="cupo" v-model="form2.cupos" type="number" required placeholder=""></b-form-input><br>
            </b-form-group>
            <b-form-group id="input-group-2" label="Inscritos del curso" label-for="inscritos">
              <b-form-input id="inscritos" v-model="form2.inscritos" type="number" required
                placeholder=""></b-form-input><br>
            </b-form-group>
            <b-form-input id="duracion" v-model="form2.duracion" type="text" required
              placeholder="Duración del curso"></b-form-input><br>
            <b-form-group id="input-group-3" label="Fecha de registro" label-for="fecha">
              <b-form-input id="fecha" v-model="form2.fecha_registro" type="text" placeholder=""
                readonly></b-form-input><br>
            </b-form-group>
            <b-form-group id="input-group-4" label="Terminado" label-for="completado">
              <b-form-checkbox v-model="form2.completado" name="completado" switch>
                <b> {{ form2.completado ? 'Sí' : 'No' }}</b>
              </b-form-checkbox>
            </b-form-group><br>
            <b-form-group id="input-group-5" label="Costo del curso" label-for="costo">
              <b-form-input id="costo" v-model="form2.costo" type="text" required placeholder=""></b-form-input><br>
            </b-form-group>
            <b-form-textarea id="descripcion" v-model="form2.descripcion" placeholder="Descripcón del curso" rows="3"
              max-rows="6"></b-form-textarea> <br>
            <b-button type="submit" variant="success">EDITAR</b-button>
            <b-button variant="danger" v-on:click.prevent="hideModal2">CERRAR</b-button>
          </b-form>
        </b-modal>
      </div>
    </b-container>
  </div>
</template>
  
<script>
import ResumenCursos from '@/components/ResumenCursos.vue'
import { mapActions, mapState, mapGetters } from 'vuex';
import router from '@/router';
export default {
  name: 'Administracion',
  props: {},
  data() {
    return {
      form: {
        id: 0,
        img: '',
        nombre: '',
        costo: 0,
        duracion: '',
        cupos: 0,
        inscritos: 0,
        completado: false,
        fecha_registro: this.fechaActual(),
        descripcion: '',
      },
      form2: {
        id: 0,
        img: '',
        nombre: '',
        costo: 0,
        duracion: '',
        cupos: 0,
        inscritos: 0,
        completado: false,
        fecha_registro: this.fechaActual(),
        descripcion: '',
      },
      show: true,
      show2: true,
      resumen:true
    };
  },
  components: {
    ResumenCursos
  },
  computed: {
    ...mapState('cursos_module', ['datos']),
    ...mapGetters('cursos_module', ['getCursoByID']),
    ...mapState('cursos_module', ['id_curso_edit']),
  },
  methods: {
    ...mapActions('cursos_module', ['eliminarCurso']),
    ...mapActions('cursos_module', ['registrarCurso']),
    ...mapActions('cursos_module', ['editarCurso']),
    ...mapActions('cursos_module', ['setIdCursoEdit']),
    eliminarC: function (id) {
      let response = confirm("¿Estás seguro de eliminar el curso?");
      if (response) {
        this.eliminarCurso(id);
      }
    },
    onSubmit(event) {
      event.preventDefault();
      if (this.form.cupos >= this.form.inscritos) {
        let newId = this.datos.length + 1;
        this.form.id = newId;
        let data = { ...this.form };
        this.registrarCurso(data);
        this.$refs['my-modal'].hide();
        this.onReset(event);
      } else {
        alert('¡EL NUMERO DE INSCRITOS NO PUEDE SER MAYOR AL CUPO DEL CURSO!')
      }

    },
    onReset(event) {
      event.preventDefault();
      this.form.nombre = '';
      this.form.img = '';
      this.form.costo = 0;
      this.form.duracion = '';
      this.form.cupos = 0;
      this.form.inscritos = 0;
      this.form.completado = false;
      this.form.fecha_registro = this.fechaActual();
      this.form.descripcion = '';

      this.show = false;
      this.$nextTick(() => {
        this.show = true;
      })
    },
    fechaActual() {
      let currentDate = new Date();
      let day = currentDate.getDate().toString().padStart(2, '0');
      let month = (currentDate.getMonth() + 1).toString().padStart(2, '0');
      let year = currentDate.getFullYear();
      return `${day}/${month}/${year}`;
    },
    edit(event) {
      event.preventDefault();
      if (this.form2.cupos > this.form2.inscritos) {
        if(this.form2.completado === true){
          this.form2.inscritos = 0;
        }
        let data = { ...this.form2, id: this.id_curso_edit }
        this.editarCurso(data);
        this.$refs['my-modal2'].hide();
        this.clean(event);
        this.resumen =true;
      }
    },
    clean(event) {
      event.preventDefault();
      this.form2.nombre = '';
      this.form2.img = '';
      this.form2.costo = 0;
      this.form2.duracion = '';
      this.form2.cupos = 0;
      this.form2.inscritos = 0;
      this.form2.completado = false;
      this.form2.fecha_registro = this.fechaActual();
      this.form2.descripcion = '';
      this.show2 = false;
      this.$nextTick(() => {
        this.show2 = true;
      })
    },
    setCurso() {
      let curso = this.getCursoByID(this.id_curso_edit);
      this.form2.nombre = curso.nombre;
      this.form2.img = curso.img;
      this.form2.costo = curso.costo;
      this.form2.duracion = curso.duracion;
      this.form2.cupos = curso.cupos;
      this.form2.inscritos = curso.inscritos;
      this.form2.completado = curso.completado;
      this.form2.fecha_registro = this.fechaActual();
      this.form2.descripcion = curso.descripcion;
    },
    openModal2(id) {
      this.resumen =false;
      this.setIdCursoEdit(id);
      this.setCurso();
      this.$refs['my-modal2'].show();
    },
    hideModal(){
      this.$refs['my-modal'].hide();
    },
    hideModal2(){
      this.$refs['my-modal2'].hide();
      this.resumen =true;
    }
  },
  created() {
    
  },
  mounted() {}
}
</script>
  
<style scoped>
.btnAcction {
  background-color: rgba(240, 248, 255, 0) !important;
  border: none;
}

table {
  text-align: left !important;
}

.td-gris {
  background-color: rgba(155, 155, 155, 0.747);
  color: white;
  border-radius: 20px;
  padding: 10px;
  height: 40px;
  text-align: center;
  width: 50px;
  margin-left: auto;
  margin-right: auto;
}

.td-azul {
  background-color: rgb(35, 193, 255);
  color: white;
  border-radius: 20px;
  padding: 10px;
  height: 40px;
  text-align: center;
  width: 50px;
  margin-left: auto;
  margin-right: auto;
}

.td-verde {
  background-color: rgb(70, 192, 68);
  color: white;
  border-radius: 20px;
  padding: 10px;
  height: 40px;
  width: 90px;
}

.td-verde-fecha {
  background-color: rgb(70, 192, 68);
  color: white;
  border-radius: 20px;
  padding: 10px;
  height: 40px;
  width: 100px;
}

.th-terminado {
  text-align: center;
}

button {
  margin-right: 5px;
}

</style>
  