<template>
  <div class="resumen mt-5">
    <b-container>

      <b-alert show class="alert1"><b-icon icon="people-fill"></b-icon> Cantidad total de alumnos permitidos: <strong> {{ detalleFinal.a_permitidos }} </strong> alumnos.</b-alert>
      <b-alert show class="alert2 mt-3"><b-icon icon="person-check-fill"></b-icon> Cantidad total de alumnos inscritos: <strong> {{ detalleFinal.a_inscritos }} </strong> alumnos.</b-alert>
      <b-alert show class="alert3 mt-3"><b-icon icon="person-plus-fill"></b-icon> Cantidad total de cupos restantes: <strong> {{ detalleFinal.c_restantes }} </strong> alumnos.</b-alert>
      <b-alert show class="alert4 mt-3"><b-icon icon="x-circle"></b-icon> Cantidad total de cursos terminados: <strong> {{ detalleFinal.c_terminados }} </strong> cursos.</b-alert>
      <b-alert show class="alert5 mt-3">'<b-icon icon="bell-fill"></b-icon>' Cantidad total de cursos activos: <strong> {{ detalleFinal.c_activos }} </strong> cursos.</b-alert>
      <b-alert show class="alert6 mt-3">'<b-icon icon="bell-fill"></b-icon>' Cantidad total de cursos: <strong> {{ detalleFinal.c_totales }} </strong> cursos.</b-alert>
    </b-container>
  </div>
</template>
    
<script>
import { mapState } from 'vuex';
export default {
  name: 'Resumen',
  props: {},
  components: {},
  computed: {
    ...mapState('cursos_module', ['datos']),
    detalleFinal:function(){
      let totalCupos = 0;
            let totalInscripciones = 0;
            let cursosTerminados = 0;
            let cursosActivos = 0;
            let totalRestantes = 0;
            let totalCursos = this.datos.length;
            for (let index = 0; index < this.datos.length; index++) {
                let curso = this.datos[index];
                totalCupos = parseInt(totalCupos) + parseInt(curso.cupos);
                totalInscripciones = parseInt(totalInscripciones) + parseInt(curso.inscritos);
                if (curso.completado) {
                    cursosTerminados++
                } else {
                    cursosActivos++;
                }
            }

            totalRestantes = totalCupos - totalInscripciones;
            let objetoDetalleCurso = {
                a_permitidos: totalCupos,
                a_inscritos: totalInscripciones,
                c_terminados: cursosTerminados,
                c_activos: cursosActivos,
                c_totales: totalCursos,
                c_restantes: totalRestantes
            };
            return objetoDetalleCurso;
    }
  },
  methods: {},
  created() {},
  data() {return {};},
  mounted() {},
}
</script>
    
