import axios from "axios";
const cursos_module = {
    namespaced: true,
    state: {
        datos: [],
        cursos_disponibles: [],
        id_curso_edit: null,
    },
    getters: {
        getCursoByID: (state) => (id) => {
            return state.datos.find(cur => cur.id === id);
          }
    },
    mutations: {
        GET_CURSOS: (state, cursos) => {
            state.datos = cursos;
        },
        GET_CURSOS_DISPONIBLES: (state, cursos) => {
            state.cursos_disponibles = cursos;
        },
        ELIMINAR_CURSO:(state, id) =>{
            let indice = state.datos.findIndex(info => info.id == id);
            state.datos.splice(indice,1);
            let indiceDisponible = state.cursos_disponibles.findIndex(info => info.id == id);
            state.cursos_disponibles.splice(indiceDisponible,1);
        },
        REGISTAR_CURSO:(state, curso)=>{
            state.datos.push(curso);
            state.cursos_disponibles.push(curso);
        },
        EDITAR_CURSO: (state, curso) => {
            let index = state.datos.findIndex(cur => cur.id === curso.id);
            state.datos[index] = curso;
            let indiceDisponible = state.cursos_disponibles.findIndex(cur => cur.id === curso.id);
            state.cursos_disponibles[indiceDisponible] = curso;
            state.id_product_edit = null;
        },
        SET_ID_CURSO_ID: (state, id) => {
            state.id_curso_edit = id;
          },
    },
    actions: {
        getCursos: (context) => {
            let url = 'datoscursos.json';
            axios.get(url)
                .then(respuesta => {
                    context.commit('GET_CURSOS', respuesta.data.cursos);
                    let cursosDisponibles = respuesta.data.cursos.filter(curso => curso.completado === false);
                    context.commit('GET_CURSOS_DISPONIBLES', cursosDisponibles);
                    
                })
                .catch(error => {
                    console.log(error);
                })
        },
        getCursosDisponibles: (context) => {
        let cursosDisponibles = context.state.datos.filter(curso => curso.completado === false);
        context.commit('GET_CURSOS_DISPONIBLES', cursosDisponibles);

        },
        eliminarCurso:(context,id)=>{
            context.commit('ELIMINAR_CURSO', id);
        },
        registrarCurso:(context, curso)=>{
            context.commit('REGISTAR_CURSO', curso);
        },
        editarCurso: (context, curso) => {
            context.commit('EDITAR_CURSO', curso);
        },
        setIdCursoEdit: (context, id) => {
            context.commit('SET_ID_CURSO_ID', id);
        },

    },
    modules: {}
};

export default cursos_module;